@import url(https://fonts.googleapis.com/css?family=Corben);
@import url(https://fonts.googleapis.com/css?family=Nobile);
h1 {
  font-family: 'Corben', Georgia, Times, serif;
  font-size: 40px;
  line-height: 55px;
 }
  
 body {
  font-family: 'Nobile', Helvetica, Arial, sans-serif;
  font-size: 13px;
 }

 input{
   font-family: 'Nobile', Helvetica, Arial, sans-serif;
   font-size: 12px;
 }

 .HoverList{
   position: relative;
   display: inline-block;
 }

 .HoverContent{
   display: none;
   position: absolute;
   top: -5px;
   left: 60px;
   background-color: #f1f1f1;
   min-width: 160px;
   box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
   z-index: 1; 
}

.HoverContent li{
  color:black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.HoverList:hover .HoverContent{
  display: block;
}

.capacity {
  background-color: green;
}

span{
  padding: 5px;
  margin: 1px;
}
.ChatGrid{
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.AllChats{
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    grid-auto-columns: auto;
    
}

.Chat{
    padding: 10px;
    border: 2px solid gray;
    border-radius: 15px;
    border-color: royalblue;
}

.MessageDisplay{
    display: block;
    width: 90%;
    height: 350px;
    overflow-y: auto;
    word-wrap: break-word;
}

#messageHandle{
    font-weight: 700;
    color: salmon;
}



h1{
    color: royalblue;
}

input{
    width: 80%;
    border-radius: 15px;
    border-style: solid;
    border-width: 1px;
    padding: 7px;
    border-color: lightblue;

}

button{
    border-radius: 15px;
    border-style: solid;
    border-width: 0px;
    padding: 7px;
    margin: 5px;
    background-color: skyblue;
    
}




